/* nav bar button css (lines 3 to 50) are from https://codepen.io/azureknight/pen/DbrdeB */

.linkbutton {
  width: 20%;
  height: 80px;
  background: #f3b9e8;
  cursor: pointer;
  -webkit-transition: all 2s ease;
  -moz-transition: all 2s ease;
  -o-transition: all 2s ease;
  transition: all 2s ease;
  display: inline-block;
}

h1{
  font-size: 50px;
}


    
h2 {
  float: left;
  width: 100%;
  height: 100%;
  text-align: center;
  margin-top: 25px;
  font-weight: 100;
  font-size: 35px;
}

&:hover {
  color: #fff;
  background: none;
}


.ease {
  width: 0;
  background: #282828;
  height: 80px;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.linkbutton:hover .ease {
  width: 100%;
}

.nohover:hover {
  color: #000;
}

.amethyst {
  background: #d7a9e9;
}

html {
  background-color: #f3b9e8;
  color: #546125;
  text-shadow: 3px 3px 3px #ababab;
  text-align: center;
}

h3 {
  float: left;
  width: 100%;
  text-align: center;
  font-weight: 100;
}

h4 {
  float: left;
  width: 100%;
  height: 75%;
  text-align: center;
  font-weight: 100;
}

table {
  margin-left: auto;
  margin-right: auto;
}

header {
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 100px;
}

.App-link {
  color: #454E22;
}

a {
  color: inherit;
  text-decoration: none;
  font-weight: 100px;
}

.pictures {
  width: 25%;
  height: 25%;
  padding-left: 1%;
  padding-right: 1%;
}

.border {
  margin-top: -10px;
  width:100%;
}


.homePage, .aboutPage, .contactPage, .artPage, .resumePage, .projectsPage {
  padding: 250px;
}

p {
  font-size: 20px;
}